.header
{
  color: rgb(54, 115, 115);
  text-align: center;
}
*
{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: white;
  margin: 0;
  padding: 0;
}
body{
  background: url("https://img.freepik.com/free-vector/bokeh-defocused-background_23-2148497833.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.app
{
  background: linear-gradient(5deg, #dcf06b 0%, #f48ecd 100%);
  display: flex;
  width: 95%;
  min-height: 200px;
  height: min-content;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0,0,0,0.75);
  justify-content: space-evenly;
}
.score-section{
  display: flex;
  font-size: 30px;
  font-weight: 700;
  color:rgb(212, 96, 42) ;
  align-items: center;
}
.question-section
{
  width: 100%;
  position: relative;
}
.question-count
{
  margin-bottom: 20px;
  font-size: 10px;
}
.question-count span
{
  font-size: 60px;
}
.question-text
{
  margin-bottom: 20px;
}
.answer-section
{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}
.answer-section button
{
  width: 100%;
  font-size: 16px;
  color: black;
  background: linear-gradient(80deg, #f71aa2 0%, #c0f7b6 80%);
  border-radius: 5px;
  display: flex;
  padding: 10px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid #918d5e;
  cursor: pointer;
}